import React, { Component } from 'react';
import '../index.css';

class Square extends Component{
    constructor(props){
        super(props);
    }
    render(){
        var width = this.props.row * 0.75;
        var opacity = this.props.row*20;
        var size = 'calc('+width+ 'vw)';
        var size_color= 'calc('+width/1.5+ 'vw - 5px)'
        var size_sm = 'calc('+width/1.5+ 'vw)';
        var size_xsm = 'calc('+width/2.25+ 'vw)';
        if(this.props.color ==="green" && this.props.active === true){
            return(
                <div className="square square-outer"style={{opacity: opacity+'%', width: size, height: size_sm}}>
                <div className="square square-green mx-auto" style={{ width: size, height: size_color}}>
                <div className="square square-white mx-auto" style={{ width: size_sm, height: size_xsm}}/>
                </div>
                </div>);
        } else if(this.props.color ==="red" && this.props.active === true){
            return(
                <div className="square square-outer"style={{opacity: opacity+'%', width: size, height: size_sm}}>
                <div className="square square-red mx-auto" style={{ width: size, height: size_color}}>
                <div className="square square-white mx-auto" style={{ width: size_sm, height: size_xsm}}/>
                </div>
                </div>);
        } else if(this.props.color ==="yellow" && this.props.active === true){
            return(
                <div className="square square-outer"style={{opacity: opacity+'%',width: size, height: size_sm}}>
                <div className="square square-yellow mx-auto" style={{ width: size, height: size_color}}>
                <div className="square square-white mx-auto" style={{ width: size_sm, height: size_xsm}}/>
                </div>
                </div>);
        } else if(this.props.color ==="blue" && this.props.active === true){
            return(
                <div className="square square-outer"style={{opacity: opacity+'%',width: size, height: size_sm}}>
                <div className="square square-blue mx-auto" style={{ width: size, height: size_color}}>
                <div className="square square-white mx-auto" style={{ width: size_sm, height: size_xsm}}/>
                </div>
                </div>);
        } else if(this.props.color ==="orange" && this.props.active === true){
            return(
                <div className="square square-outer"style={{opacity: opacity+'%',width: size, height: size_sm}}>
                <div className="square square-orange mx-auto" style={{ width: size, height: size_color}}>
                <div className="square square-white mx-auto" style={{ width: size_sm, height: size_xsm}}/>
                </div>
                </div>);
        } else{
            if(this.props.color ==="orange" && this.props.active === false && this.props.row === 10){
                if(this.props.isPressed){
                    return  (
                        <div className="square square-border border-orange mx-auto bg-white" style={{opacity:30+'%', width: size, height: size_color}}>
                        </div>);

                }
                return (
                <div className="square square-border border-orange mx-auto" style={{opacity:30+'%', width: size, height: size_color}}>
                </div>);
            } else if(this.props.color ==="red" && this.props.active === false && this.props.row === 10){
                if(this.props.isPressed){
                    return  (
                        <div className="square square-border border-red mx-auto bg-white" style={{opacity:30+'%', width: size, height: size_color}}>
                        </div>);

                }
                return (
                <div className="square square-border border-red mx-auto" style={{opacity:30+'%', width: size, height: size_color}}>
                </div>);

            } else if(this.props.color ==="blue" && this.props.active === false && this.props.row === 10){
                if(this.props.isPressed){
                    return  (
                        <div className="square square-border border-blue mx-auto bg-white" style={{opacity:30+'%', width: size, height: size_color}}>
                        </div>);

                }
                return (
                <div className="square square-border border-blue mx-auto" style={{opacity:30+'%', width: size, height: size_color}}>
                </div>);

            } else if(this.props.color ==="yellow" && this.props.active === false && this.props.row === 10){
                if(this.props.isPressed){
                    return  (
                        <div className="square square-border border-yellow mx-auto bg-white" style={{opacity:30+'%', width: size, height: size_color}}>
                        </div>);

                }
                return (
                <div className="square square-border border-yellow mx-auto" style={{opacity:30+'%', width: size, height: size_color}}>
                </div>);


            } else if(this.props.color ==="green" && this.props.active === false && this.props.row === 10){
                if(this.props.isPressed){
                    return  (
                        <div className="square square-border border-green mx-auto bg-white" style={{opacity:30+'%', width: size, height: size_color}}>
                        </div>);

                }
                return (
                <div className="square square-border border-green mx-auto" style={{opacity:30+'%', width: size, height: size_color}}>
                </div>);

            }
            return <div className = "square" style={{opacity: opacity, width: size, height: size_sm}}></div>
        }
    };

}

export default Square;